                    //     .then(res => {
                    //         if (res.code === 1) {
                    //             this.error = res.msg;
                    //             this.errStatus = true;
                    //             return;
                    //         }
                    //         if (this.check) {
                    //             sessionStorage.user = JSON.stringify(values);
                    //         } else {
                    //             sessionStorage.clear();
                    //         }
                    //         sessionStorage.userInfo = JSON.stringify({
                    //             avatar: res.data.avatar,
                    //             username: res.data.username
                    //         });
                    //         // this.$api.common.authority().then(res => {
                    //         //     localStorage.authority = JSON.stringify(
                    //         //         res.data.authority
                    //         //     );
                    //         //     this.$eventBus.emit("updateRole");
                    //         //     const routes = this.$router.options.routes;
                    //         //     routes.forEach(it => {
                    //         //         if( res.data.authority[0].name=== it.meta.name){
                    //         //             this.$router.push({ name:  it.name});
                    //         //         }
                    //         //     });
                    //         // });

                    //         // this.$router.push({ name: "orderUserList" });
                    //     });
                }
            });
        },
        onChange(e) {}
    },
    watch: {},
    mounted() {
        this.$nextTick(() => {
            this.form.validateFields();
            if (sessionStorage.user) {
                let user = JSON.parse(sessionStorage.user);
                this.check = user.check * 1 === 0 ? false : true;
                this.form.setFields({
                    login: { value: user.login }
                });
                this.form.setFields({
                    passwd: { value: user.passwd }
                });
            }
        });
    }
};
